body {
  width: 100%;
  height: 100vh;
  background: lightslategrey;
}
.App {
  width: 400px;
  margin: 0 auto;
  background: royalblue;
  box-shadow: 0 0 5px;
  margin-top: 10px;
}
.value-container {
  padding-top: 1.75%;
}
.value-container input {
  display: block;
  width: 95%;
  border: none;
  height: 30px;
  text-align: right;
  padding: 0 10px;
  margin: 0 auto;
  font-size: 22px;
  font-weight: 700;
  color: lightslategrey;
}
.buttons-container {
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.buttons-container button {
  width: 20%;
  height: 30px;
  line-height: 30px;
  margin: 2%;
  border: none;
  outline: none;
  background: #e65f5f;
  color: white;
  font-weight: 700;
}
.buttons-container button:hover {
  box-shadow: 0 0 5px;
}
